.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important; }
  .sr-only:focus, .sr-only:active {
    clip: auto !important;
    -webkit-clip-path: none !important;
    clip-path: none !important;
    height: auto !important;
    margin: auto !important;
    overflow: visible !important;
    width: auto !important;
    white-space: normal !important; }

.offscreen {
  position: absolute;
  top: -9999em;
  left: -9999em; }

.hide {
  display: none; }

.show {
  display: initial; }

.invisible {
  visibility: hidden; }

.clear {
  display: block;
  clear: both; }

.clear-after:after {
  display: table;
  clear: both;
  content: ''; }

.float-center {
  display: block;
  margin-right: auto;
  margin-left: auto; }

.float-left {
  float: left; }

.float-right {
  float: right; }

.text-left {
  text-align: left; }

.text-right {
  text-align: right; }

.text-center {
  text-align: center; }

.text-justify {
  text-align: justify; }

